import { Component, h } from '@stencil/core';

@Component({
  tag: 'chat-list',
  shadow: true
})
export class ChatList {
  private interactions: { text: string; hour: string }[] = [
    { text: 'Hello, how can I help you?', hour: '12:00 PM' },
    { text: 'I need assistance with my order.', hour: '12:01 PM' },
    { text: 'Sure, let me look that up for you.', hour: '12:02 PM' }
  ];

  render() {
    return (
      <div>
        {this.interactions.map(interaction => (
          <chat-bubble text={interaction.text} hour={interaction.hour}></chat-bubble>
        ))}
      </div>
    );
  }
}

